<template>
  <section>
    <loading :active.sync="isLoading"></loading>
    <div class="mt-6 mt-sm-4 d-flex justify-content-between align-items-center pr-3">
      <button class="btn btn-primary btn-lg" @click="openModal(true)">
        
      </button>
      <Pagination :pagination="pagination" @getPage="getProducts"></Pagination>
    </div>
    <div class="table-responsive mt-3">
      <table class="table">
        <thead>
          <tr>
            <th width="120"></th>
            <th></th>
            <th width="120" class="text-right"></th>
            <th width="120" class="text-right"></th>
            <th width="100"></th>
            <th width="120"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in products" :key="item.id">
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <td class="text-right">{{ item.origin_price | currency }}</td>
            <td class="text-right">{{ item.price | currency }}</td>
            <td>
              <!--  === 1 -->
              <span v-if="item.is_enabled === 1" class="text-success"
                ></span
              >
              <span v-else class="text-danger"></span>
            </td>
            <td>
              <div class="btn-group" role="group" aria-label="Basic example">
                <button
                  class="btn btn-outline-primary btn-sm"
                  data-toggle="modal"
                  data-target="#editModal"
                  @click="openModal(false, item)"
                >
                  
                </button>
                <button
                  class="btn btn-danger btn-sm"
                  data-toggle="modal"
                  data-target="#deleteModal"
                  @click="openModal(false, item, true)"
                >
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- Add & Edit Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                    v-model="tempProduct.imageUrl"
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i
                      class="fas fa-spinner fa-pulse"
                      v-if="status.fileUpLoading"
                    ></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img class="img-fluid" alt :src="tempProduct.imageUrl" />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                    v-model="tempProduct.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempProduct.category"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                      v-model.number="tempProduct.unit"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      v-model.number="tempProduct.origin_price"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                      v-model.number="tempProduct.price"
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                    v-model="tempProduct.description"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                    v-model="tempProduct.content"
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model.number="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled"
                      ></label
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="updateProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
               {{ tempProduct.title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body"> {{ tempProduct.title }} ?</div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button type="button" class="btn btn-danger" @click="deleteProduct">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery';
import Pagination from '../../Pagination';

export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isNew: false,
      isLoading: false,
      status: {
        fileUpLoading: false,
      },
      pagination: {},
      page: 1,
    };
  },
  created() {
    this.getProducts();
  },
  methods: {
    getProducts(page = 1) {
      this.page = page;
      const api = `${process.env.API_URL}/api/${process.env.API_PATH}/admin/products?page=${page}`;
      const self = this;
      self.isLoading = true;
      this.$http.get(api).then((response) => {
        self.products = response.data.products;
        // console.log('products', self.products);
        self.isLoading = false;
        self.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item, isDelete = false) {
      if (isNew) {
        this.tempProduct = {};
        //  ()
        this.isNew = true;
      } else {
        // assign  by reference ( item)
        //  temp 
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }

      if (isDelete) {
        $('#deleteModal').modal('show');
      } else {
        $('#productModal').modal('show');
      }
    },
    updateProduct() {
      let api = `${process.env.API_URL}/api/${process.env.API_PATH}/admin/product`;
      let APIMethod = 'post';
      const self = this;
      if (!self.isNew) {
        api = `${process.env.API_URL}/api/${process.env.API_PATH}/admin/product/${self.tempProduct.id}`;
        APIMethod = 'put';
      }
      // product  post  tempProduct 
      // $http[APIMethod]  object[property]]  API method
      this.$http[APIMethod](api, { data: self.tempProduct }).then(
        (response) => {
          if (response.data.success) {
            //  Modal
            $('#productModal').modal('hide');
            self.getProducts(self.page);
          } else {
            $('#productModal').modal('hide');
            self.getProducts(self.page);
            self.$bus.$emit('message:push', '', 'danger');
          }
        },
      );
    },
    deleteProduct() {
      const api = `${process.env.API_URL}/api/${process.env.API_PATH}/admin/product/${this.tempProduct.id}`;
      const self = this;
      this.$http.delete(api).then((response) => {
        if (response.data.success) {
          //  Modal */
          $('#deleteModal').modal('hide');
          self.getProducts(this.page);
        } else {
          // 
          $('#deleteModal').modal('hide');
          self.getProducts(self.page);
          self.$bus.$emit('message:push', response.data.message, 'danger');
        }
      });
    },
    uploadFile() {
      // 
      //  ref  input file 
      const uploadedFile = this.$refs.files.files[0];
      const self = this;
      // formData  ajax 
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const api = `${process.env.API_URL}/api/${process.env.API_PATH}/admin/upload`;
      self.status.fileUpLoading = true;
      this.$http
        .post(api, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((response) => {
          if (response.data.success) {
            //  data  imageUrl url  Vue  data 
            // self.tempProduct.imageUrl = response.data.imageUrl
            //  $set  Vue  data 
            self.$set(self.tempProduct, 'imageUrl', response.data.imageUrl);
            self.status.fileUpLoading = false;
            //  input file
            $('#customFile').val('');
          } else {
            let errorMessage = response.data.message;
            if (typeof response.data.message === 'object') {
              errorMessage = response.data.message.message;
            }
            this.$bus.$emit('message:push', errorMessage, 'danger');
            self.status.fileUpLoading = false;
          }
        });
    },
  },
  components: {
    Pagination,
  },
};
</script>

<style></style>



