<template>
  <section class="coupon container mt-sm-5 fixed-margin-top">
    <h1 class="coupon-title h3 text-center"></h1>
    <div class="coupon-content row align-items-center mt-3">
      <div class="col-12 col-lg-6 px-4 py-3 p-lg-5 ">
        <h2 class="mr-auto mb-0 text-center text-white"> 8 </h2>
      </div>
      <div class="col-12 col-lg-6 px-4 pb-3 p-lg-5 text-center">
        <span class="text-white"></span>
        <button
          type="button"
          class="btn btn-primary btn-lg btn-block d-block mt-2"
          v-if="!isCoupon"
          @click="isCoupon = true"
        >
          
        </button>
        <div v-else>
          <span class="h2 mt-2 font-weight-bold text-white coupon-code">
            <span>BAT008</span>
            <i
              class="far fa-copy text-white icon-copy active"
              @click.prevent="copyCouponCode"
              v-if="!isCopied"
            ></i>
            <i class="far fa-check-circle icon-copy" v-else></i>
          </span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      isCoupon: false,
      isCopied: false,
    };
  },
  methods: {
    // copy 
    copyCouponCode() {
      //  input
      const temp = $('<input>');
      //  input append  body
      $('body').append(temp);
      //  coupon ()  input  select 
      temp.val($('.coupon-code').text().trim())
        .select();
      //  execCommand:copy 
      document.execCommand('copy');
      //  input
      temp.remove();
      this.isCopied = true;
    },
  },
};
</script>

<style></style>



